
// Variables
@import "variables";

.navbar{
	margin-bottom: 0;
}
.navbar-custom{
	background-color: #2ef70a;
	color: #0f9a27;
}
.circle-shopping-cart{
	background-color: #0f9a27;
	color: red;
	border-radius:  50%;
	padding: 1em;
}
.products-container{
	.product{
		margin-top: 1em;
	}
}
.product-avatar{
	max-width: 100%;
}
.big-padding{
	padding: 30px;
}


.blue-grey{
	background-color: #37474f;
}


.white-text{
	color: white;
}



.table{
	background-color: white;
}
.absolute{
	position: absolute;;

}

.floating{
	position:fixed;
	bottom: 2em;
	right: 2em;
	z-index: 100;
}


.white{
	background-color: white;
}


.red-text{
	color: rgb(200,50,50);
}


.no-padding{
	padding: 0px;
}

.top-space{
	margin-top: 1em;
}


.large-padding{
	padding: 10px 15px;
}


.no-transform{
	text-transform: none;;
}


.no-margin{
	margin: 0px;
}



.inline-block{
	display: inline-block;
}



.product{
	width: 600px;
	max-width: 100%;
	padding: 1em;
	position: relative;

	h1{
		padding-bottom: 10px;
		border-bottom: solid 1px;
	}

	.action{
		right: 1em;
		top: 10px;
	}


}
.sale-data{
	padding: 1em;
	text-align: center;
	border-right: solid 1px #222;
	span{
		display: block;
		font-size: 2.2em;
		color: #3f51b5;
	} 
}
.creado{
	background-color: #d4e157;
}
.highlight{
	-webkit-animation:highlight 1.5s;
	animation: highlight 1.5s; 
}
@-webkit-keyframes highlight{
	50% {
		background-color: #ffff00;
		color: black;
	}
	100%{
		background-color: inherit;
	}
	@keyframes highlight{
		50% {
			background-color: #ffff00;
			color: black;
		}
		100% {
			background-color: inherit;
		}
	}
}

